/*
=====================
RESPONSIVE MIXINS
=====================
*/
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  border-collapse: collapse; }

button {
  cursor: pointer; }

html,
body {
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 1.4;
  color: #444; }

/*
====================
SOLID BUTTONS
====================
*/
.btn-solid--brand {
  padding: 0.75rem 1rem;
  border-radius: 7px;
  text-decoration: none;
  display: inline-block;
  transition: all 250ms ease-in-out;
  background-color: #29652d;
  color: #fff; }
  .btn-solid--brand:hover {
    background-color: #3f9b45; }

.btn-solid--brand-two {
  padding: 0.75rem 1rem;
  border-radius: 7px;
  text-decoration: none;
  display: inline-block;
  transition: all 250ms ease-in-out;
  background-color: #bdd341;
  color: #fff; }
  .btn-solid--brand-two:hover {
    background-color: #d3e17f; }

.btn-solid--white {
  padding: 0.75rem 1rem;
