.eyebrow {
  background-color: #29652d;
  display: none; }
  @media screen and (min-width: 1080px) {
    .eyebrow {
      display: block; } }

.eyebrow * {
  font-size: 0.8rem;
  color: #fff; }

.eyebrow ul {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
  display: flex; }

.eyebrow li {
  font-weight: bold; }
  .eyebrow li:first-child {
    flex-grow: 2; }

.eyebrow a {
  display: inline-block;
  padding: 0.75rem 0.5rem;
  text-decoration: none; }

.site-header > .site-width {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.site-header-list {
  list-style-type: none;
  padding-left: 0;
  margin: 0;
  display: flex;
  flex-direction: column; }
  @media screen and (min-width: 1080px) {
    .site-header-list {
      align-items: center;
      flex-direction: row; } }

.site-navigation--header {
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  top: 5.5rem;
  z-index: 2;
  background-color: #fff; }
  @media screen and (min-width: 1080px) {
    .site-navigation--header {
      position: static;
      top: auto;
      background-color: transparent;
      display: block; } }
  @media screen and (min-width: 1080px) {
    .site-navigation--header form {
      display: none; } }
  .site-navigation--header input {
    width: calc(100% + 2rem);
    position: relative;
    left: -1rem;
    font-family: 'Open Sans', Helvetica, Arial, sans-serif;
    font-size: 1rem;
    padding: 1rem 2rem;
    background-color: #f0f0f0;
    border: 0; }
  .menu-open .site-navigation--header {
    display: block; }

.site-header .menu-item--logo-container img {
  width: 100%;
  max-width: 225px;
  display: block; }

.menu-item--logo-container a {
  display: block;
  padding: 1rem 0; }

.site-header-list .menu-item--logo-container a:after {
  display: none; }

.site-header-list li {
  border-bottom: 1px solid #ccc; }
  @media screen and (min-width: 1080px) {
    .site-header-list li {
      padding-left: 0;
      padding-right: 0;
      border-bottom: 0; } }

.site-header-list a {
  display: block;
  color: #29652d;
  text-decoration: none;
  padding: 1rem; }
  @media screen and (min-width: 1080px) {
    .site-header-list a {
      margin: 0 0.5rem;
      padding: 0; } }

.site-header-list > a:after {
  content: url(/dist/img/accordian-toggle.svg);
  float: right; }
  @media screen and (min-width: 1080px) {
    .site-header-list > a:after {
      content: '|';
      display: inline-block;
      margin-left: 0.5rem;
      margin-right: -0.5rem; } }

.site-header-list li:last-child a:after {
  display: none; }

.shopping-cart-list-item img {
  max-width: 35px; }

@media screen and (min-width: 1080px) {
  .menu-item--menu-toggle {
    display: none; } }

.accordian-toggle--site-header {
  display: block;
  background-color: transparent;
  border: 0;
  padding: 1rem;
  width: 100%;
  text-align: left;
  color: #29652d;
  font-size: 1rem;
  font-family: 'Open Sans', Helvetica, Arial, sans-serif; }
  .accordian-toggle--site-header:after {
    content: url(/dist/img/accordian-toggle.svg);
    width: 20px;
    float: right;
    transform: rotateZ(0deg);
    transition: transform 250ms ease-in-out; }
    @media screen and (min-width: 1080px) {
      .accordian-toggle--site-header:after {
        width: auto;
        content: '|';
        display: inline-block;
        margin-left: 0.5rem;
        margin-right: -0.5rem;
        transform: rotateZ(0); } }
  .accordian-toggle--site-header:focus {
    outline: 0; }
  .sub-menu--level-one .accordian-toggle--site-header {
    padding-left: 2rem; }

.accordian-toggle--site-header.active:after {
  transform: rotateZ(180deg); }
  @media screen and (min-width: 1080px) {
    .accordian-toggle--site-header.active:after {
      transform: rotateZ(0); } }

[class*="sub-menu"] {
  display: none;
  list-style-type: none;
  padding-left: 0; }
  [class*="sub-menu"].accordian-open {
    display: block; }
    @media screen and (min-width: 1080px) {
      [class*="sub-menu"].accordian-open {
        display: none; } }
  [class*="sub-menu"] > li:first-child {
    border-top: 1px solid #ccc; }

.sub-menu--level-one.accordian-open > li:first-child {
  border-top: 5px solid #bdd341; }

.sub-menu--level-one.accordian-open > li:last-child {
  border-bottom: 5px solid #bdd341; }

.sub-menu--level-two {
  background-color: #29652d;
  padding: 0 1rem; }
  .sub-menu--level-two > li {
    padding: 2rem 0 1rem; }
    .sub-menu--level-two > li:first-child {
      border-top: 0; }
    .sub-menu--level-two > li:last-child {
      border-bottom: 0; }

.sub-menu--level-two h6 {
  color: #fff;
  font-size: 1.25rem;
  margin-bottom: 0.5rem; }

.sub-menu--level-two.accordian-open .sub-menu--level-three {
  display: flex;
  flex-wrap: wrap; }
  .sub-menu--level-two.accordian-open .sub-menu--level-three li {
    border: 0;
    flex-basis: 50%; }
  .sub-menu--level-two.accordian-open .sub-menu--level-three a {
    color: #bdd341;
    padding: 0 0 1em;
    font-size: 0.9rem; }

.search-bar-container {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 2;
  background-color: rgba(41, 101, 45, 0.9); }

.search-bar-container-items {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.search-bar-container .btn-solid--brand-two {
  margin-top: 0.7rem;
  margin-bottom: 0.7rem;
  font-size: 0.9rem;
  padding: 0.5rem 1rem;
  margin-right: 1rem; }
  .search-bar-container .btn-solid--brand-two:last-of-type {
    margin-right: 0; }

.search-bar-container form {
  flex-grow: 2; }

.search-bar-container input {
  font-family: 'Open Sans', Helvetica, Arial, sans-serif;
  border-radius: 5px;
  padding: 0.5rem;
  border: 0;
  width: calc(100% - 1rem);
  font-size: 0.75rem; }

.hero {
  background-image: url(http://kyrep.fccmz.servertrust.com/v/vspfiles/templates/gfp-test/img/homepage-hero.jpg);
  background-size: cover;
  background-position: top center;
  position: relative;
  z-index: 1;
  text-align: center;
  padding: 4rem 0 3rem;
  color: #fff;
  border-bottom: 7px solid #bdd341;
  box-shadow: inset 0 0 100px rgba(0, 0, 0, 0.5); }
  @media screen and (min-width: 600px) {
    .hero {
      padding: 10rem 0 8rem; } }
  .hero:after {
    content: '';
    position: absolute;
    top: calc(50% + 3rem);
    left: 50%;
    width: 50%;
    background-color: #000;
    height: 50%;
    transform: translate(-50%, -50%);
    filter: blur(25px);
    opacity: 0.5;
    z-index: -1;
    padding-bottom: 5rem; }

.hero h1 {
  font-size: 1.8rem;
  text-shadow: 0px 0px 10px black; }
  @media screen and (min-width: 600px) {
    .hero h1 {
      font-size: 3rem; } }

.hero.full-width {
  position: relative;
  width: 100vw;
  top: -3rem;
  bottom: -3rem;
  left: 50%;
  transform: translateX(calc(-50% - 8px)); }

.site-footer {
  background-color: #29652d;
  color: #fff;
  padding: 2rem 0; }

.site-footer h2 {
  font-size: 1.3rem;
  line-height: 1.4;
  font-weight: normal; }

.site-footer a {
  color: #fff;
  text-decoration: none; }

.footer-wrap {
  display: flex;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 5rem; }

.footer-wrap > div {
  flex-basis: 33%; }

.footer-links ul {
  list-style-type: none;
  padding-left: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(75px, 150px)); }

.footer-links li {
  font-size: 0.8rem;
  margin-bottom: 1rem; }

.footer-links a:hover {
  text-decoration: underline; }

.footer-payment-list {
  display: flex;
  justify-content: center;
  list-style-type: none;
  padding: 2rem 0 1rem; }

.footer-payment-list li {
  margin-right: 1rem; }

.footer-payment-list svg {
  max-width: 50px;
  max-height: 20px; }

.footer-payment-list path {
  fill: #fff; }

.footer-copyright {
  text-align: center;
  font-size: 0.75rem; }

